import React, { Component } from 'react';
import PropTypes from 'prop-types';
import PageHeader from './PageHeader';
import Section from './Section';
import SubSection from './SubSection';
import Checkbox from './Checkbox';
import RadioButtons from './RadioButtons';
import RadioButton from './RadioButton';
import SaveButton from './SaveButton';
import NotificationSystem from 'react-notification-system';
import TooltipMarker from './TooltipMarker';
import PickupBarOptions from './PickupBarOptions';
import {BANNER_DISABLE_OPTION} from '../../../../../utilities/src/setting_config';
import {EXTENSION_SETTING_FAQ_LINK} from '../../../../../utilities/src/common_const'

export default class Setting extends Component {

    constructor(props) {
        super(props);
        this._notificationSystem = null;
        this._addNotification = (event) => {
            event.preventDefault();
            if (this._notificationSystem != null) {
                this._notificationSystem.addNotification({
                    message: '設定の保存が完了しました。',
                    level: 'success'
                });
            }
        };

        this.state = {
            isDebug: false
        }
    }

    componentDidMount() {
        let count = 0;
        let startTime;

        const handleKeyPress = (event) => {
            if (event.key === 'r') {
                count += 1;
                if (count === 1) {
                    startTime = new Date().getTime();
                }
                if (count === 5) {
                    const endTime = new Date().getTime();
                    const elapsedTime = endTime - startTime;
                    if (elapsedTime <= 3000) {
                        // User typed "r" 5 times in less than 3 seconds
                        this.setState({
                            isDebug: !this.state.isDebug
                        });
                    }
                    count = 0;
                }
            } else {
                count = 0;
            }
        };

        window.addEventListener('keydown', handleKeyPress);

        this.cleanup = () => {
            window.removeEventListener('keydown', handleKeyPress);
        };
    }

    componentWillUnmount() {
        this.cleanup();
    }

    render() {
        const divStyle = {
            width: "680px",
            margin: "10px auto",
            padding: "0 20px"
        };
        const notificationStyle = {
            NotificationItem: { // Override the notification item
                DefaultStyle: { // Applied to every notification, regardless of the notification level
                    margin: '10px 5px 2px 1px'
                },

                success: { // Applied only to the success notification item
                    color: 'black'
                }
            }
        };
        const faqSubsection = '楽天からのお得な情報を表示する「Pickup」や「Rebates」「クーポン」の表示について設定します。※詳細は <a style="color: blue;text-decoration:underline;" href="' + EXTENSION_SETTING_FAQ_LINK + '" target="_blank">こちら</a> をご覧ください';
        return (
            <div id="setting_content" style={divStyle} ref={(c) => { this._settings_div = c; }}>
                <NotificationSystem ref={(c) => { this._notificationSystem = c; }} style={notificationStyle} />

                <PageHeader  title="設定" div_class="page-header-div" />
                <PageHeader  title="楽天ウェブ検索" div_class="page-sub-header-div" />

                <Section>
                    <SubSection title='検索バー' image='/img/options_searchbar.png'>
                        <Checkbox id='fixed' label='検索バーを常に表示する' />
                    </SubSection>

                    <SubSection>
                        <Checkbox id='hightlight' label='ハイライト機能検索を利用する' />
                        <TooltipMarker>
                            ハイライト検索機能とは？<br /> ウェブページ内の文字を選択すると、検索バーが表示され <br /> 簡単に検索することができます。
                        </TooltipMarker>
                    </SubSection>

                    <SubSection title={<a key="default-link"
                                    href="https://websearch.rakuten.co.jp/?scid=wi_grp_setting_link_websearch"
                                    target="_blank" rel="noopener noreferrer">楽天ウェブ検索TOP<img src="/img/options_external_link.png"/></a>}>
                    <Checkbox id='default' label='ブラウザ起動時に「新しいタブを開く」機能を利用する'/>
                    </SubSection>

                    <SubSection title='検索履歴' image='/img/options_history.png' height="61">
                        <Checkbox id='searchhistory' label='検索履歴を利用する'/>
                    </SubSection>

                    <SubSection title='楽天からのおすすめ情報の表示' subtitle={faqSubsection}>
                        <RadioButtons id='rebates_options' defaultOptionIndex={0}>
                            <RadioButton label='すべての機能を使用する（推奨）' value={BANNER_DISABLE_OPTION.BANNER_POPUP_USE} />
                            {
                                 this.props.isSearchSERPPageGo=="1" ?
                                 <RadioButton label='検索サイト上でPickup機能のポップアップ表示を' value={BANNER_DISABLE_OPTION.BANNER_DISABLE_SEARCH}> <PickupBarOptions id = "options_value_search"/> オフにする</RadioButton>
                                 : <React.Fragment />
                            }
                            <RadioButton label='全サイト上ですべてのポップアップ表示を' value={BANNER_DISABLE_OPTION.BANNER_DISABLE_ALL}> <PickupBarOptions id = "options_value_all"/> オフにする</RadioButton>
                            <RadioButton label='機能を停止する' value={BANNER_DISABLE_OPTION.NO_USE} tooltip={['おすすめ情報機能を停止された場合', <br key='br0'/>, 'WEB閲覧履歴の暗号化は停止されます。']}/>
                        </RadioButtons>
                    </SubSection>
                </Section>

                <PageHeader title="楽天アフィリエイト" div_class="page-sub-header-div" />
                <Section>
                    <SubSection title='楽天アフィリエイトのアフィリエイトリンク作成をポップアップ表示する機能を提供しています'>
                        <RadioButtons id='affiliate_options' defaultOptionIndex={0}>
                            <RadioButton label='楽天アフィリエイトのポップアップを表示する' />
                            <RadioButton label='楽天アフィリエイトのポップアップ機能を停止する' />
                        </RadioButtons>
                    </SubSection>
                </Section>

                <div style={{ display: this.state.isDebug ? 'block' : 'none' }}>
                    <PageHeader title="Debug" div_class="page-sub-header-div" />
                    <Section>
                        <SubSection title="Only use for development/debugging">
                            <label htmlFor="runa_bta_endpoint" style={{ marginRight: '10px' }}>RUNA BTA endpoint</label>
                            <input id="runa_bta_endpoint" type="text" style={{ width: '300px' }}/>
                        </SubSection>
                    </Section>
                </div>

                <SaveButton id="save_setting" onClick={this._addNotification}/>
            </div>
        );
    }
}
Setting.propTypes = {
    isSearchSERPPageGo: PropTypes.string
};