import React, { Component } from 'react';
import PropTypes from 'prop-types';

export default class Section extends Component {

    constructor(props) {
        super(props);
    }

    render() {
        return (
            <div className="section-div">
                {this.props.children}
            </div>
        );
    }
}

Section.propTypes = {
    children: PropTypes.any
};