import React, { Component } from 'react';
import PropTypes from 'prop-types';

export default class TooltipMarker extends Component {

    constructor(props) {
        super(props);
    }

    render() {
        return (
            <div className="tooltip-marker">
                <div className="tooltip">{this.props.children}</div>
                <label>{'!'}</label>
            </div>
        );
    }
    static mayBeRenderWithText(text) {
        return (text) ? <TooltipMarker>{text}</TooltipMarker>
                      : null;
      }
}

TooltipMarker.propTypes = {
    children: PropTypes.any
};
