import React, { Component } from 'react';
import PropTypes from 'prop-types';
import TooltipMarker from './TooltipMarker';

export default class RadioButton extends Component {

    constructor(props) {
        super(props);
    }

    render() {
        return (
            <div>
                <label className="radio-label">{this.props.label}
                    <input type="radio" name={this.props.group_name}
                           defaultChecked={this.props.checked_by_default}
                           value={this.props.value} />
                    <span className="radio-mark"></span>
                    {this.props.children}
                </label>
                { TooltipMarker.mayBeRenderWithText(this.props.tooltip) }
            </div>
        );
    }
}

RadioButton.propTypes = {
    group_name: PropTypes.string,
    checked_by_default: PropTypes.bool,
    label: PropTypes.string.isRequired,
    value: PropTypes.number,
    tooltip: PropTypes.any,
    children: PropTypes.any
};
