import React, { Component } from 'react';
import PropTypes from 'prop-types';

export default class PickupBarOptions extends Component {
    constructor(props) {
        super(props);
        this.state = {
            dropdownValue: 0,
        }
    }

    render() {
        return (
            <select id={this.props.id}>
                <option>24時間</option>
                <option>30日間</option>
                <option>常に</option>
            </select>
        );
    }
}

PickupBarOptions.propTypes = {
    id: PropTypes.string.isRequired
}