import React, { Component } from 'react';
import PropTypes from 'prop-types';

export default class SubSection extends Component {

    constructor(props) {
        super(props);
    }

    render() {
        const imageDivStyle = {
            height:this.props.height + "px"
        }
        return (
            <div className="sub-section-div">
                {
                    !this.props.title ? null :
                    <div className="sub-section-header-div">{this.props.title}</div>
                }
                {
                    !this.props.subtitle ? null :
                    <div className="sub-section-sub-header-div" dangerouslySetInnerHTML={{ __html: this.props.subtitle }}></div>
                }
                <div style={!this.props.height ? null : imageDivStyle}>
                    {
                        !this.props.image ? null :
                        <img style={{ float:"right" }} src={this.props.image} />
                    }
                    {this.props.children}
                </div>
            </div>
        );
    }
}

SubSection.propTypes = {
    title: PropTypes.any,
    subtitle: PropTypes.any,
    image: PropTypes.string,
    height: PropTypes.string,
    children: PropTypes.any
};
