import React, { Component } from 'react';
import PropTypes from 'prop-types';

export default class SaveButton extends Component {

    constructor(props) {
        super(props);
    }

    render() {
        const buttonStyle = {
            display: "block",
            margin: "0 auto",
            padding: "15px",
            width: "220px",
            textAlign: "center",
            textDecoration: "none",
            backgroundColor: "#00A0F0",
            borderRadius: "4px",
            boxShadow: "0 2px 0 0 rgba(255,255,255,0.2) inset, 0 2px 2px 0 rgba(0,0,0,0.19)",
            fontWeight: "normal",
            fontSize: "20px",
            color: "white",
            cursor: "pointer"
        };
        return (
            <div id={this.props.id} onClick={this.props.onClick} style={buttonStyle}>保存する</div>
        );
    }
}

SaveButton.propTypes = {
    id: PropTypes.string.isRequired,
    onClick: PropTypes.func.isRequired
};
