import React from 'react';
import { render } from 'react-dom';
import { Store } from 'react-chrome-redux';
import deep from 'deep-diff';
import Setting from './components/app/Setting';
import {AFFILIATE_SETTING_OPTION} from '../../../utilities/src/affiliate_const';
import {EXTENSION_SETTING, SEARCHBAR_SETTING, BANNER_DISABLE_OPTION} from '../../../utilities/src/setting_config';
import PlatformSpecific from 'PlatformSpecific';
import {sendMessageByPort} from '../../../utilities/src/common_helper'

let port = platform_api_root.runtime.connect({name: "callFromOptionsPage"});
port.onMessage.addListener(function(msg) {
    // background returns callback
    if (msg.pref != undefined) {
        // An empty holder for reference
    }
});

(function() {

    let pageState = {
      windowIsReady : false,
      proxyStoreIsReady : false,
      settingsAreRendered : false
    };

    const proxyStore = new Store({ portName: 'background_data', extensionId: null });

    const unsubscribe = proxyStore.subscribe(() => {
        unsubscribe(); // make sure to only fire once
        pageState.proxyStoreIsReady = true;
        mayBeRenderSettings();
    });

    window.addEventListener("load", () => {
        pageState.windowIsReady = true;
        PlatformSpecific.ensureProxyStoreReady(pageState, proxyStore);
        mayBeRenderSettings();
    });


    let mayBeRenderSettings = () => {
        if (pageState.windowIsReady && pageState.proxyStoreIsReady && !pageState.settingsAreRendered) {
            pageState.settingsAreRendered = true;
            openOptionWindow();
            renderSettings();
        }
    };

    /* Wrap all the tab level setting here*/
    let searchbarSettings = new SEARCHBAR_SETTING();

    /* Wrap all the extension level settings here */
    let extensionSettings = new EXTENSION_SETTING();

    let openOptionWindow = () => {
        //setting_content
        let isSearchSERPPageGo = "0";
        if(proxyStore.getState().data && proxyStore.getState().data.abTestSettings){
            isSearchSERPPageGo = JSON.parse(proxyStore.getState().data.abTestSettings).isSearchSERPPageGo;
            }
        render(<Setting isSearchSERPPageGo={isSearchSERPPageGo} />, document.getElementById('setting_contents'));
    }

    function GoogleAnalyticsReportForUpdateSetting(lhs, rhs) {
        // Here is comparison part for rebates option setting
        let diff = deep.diff(lhs, rhs);
        if (diff != undefined) {
            sendMessageByPort(port, {title: "ReportGoogleAnalytics", diff: diff});
        }
    }

    let renderSettings = () => {
        const highlightOption = document.getElementById("hightlight");
        const fixedOption = document.getElementById("fixed");
        const defaultOption = document.getElementById("default");
        const searchHistoryOption = document.getElementById("searchhistory");
        const rebates_options = document.getElementById("rebates_options");
        const affiliate_options = document.getElementById("affiliate_options");
        const options_value_search = document.getElementById("options_value_search");
        const options_value_all = document.getElementById("options_value_all");
        const runaBtaEndpoint = document.getElementById("runa_bta_endpoint");

        // Render searchbar settings.
        searchbarSettings = proxyStore.getState().data.SearchBarSetting;
        if (searchbarSettings) {
            highlightOption.checked = searchbarSettings.SearchBarHighlight;
            fixedOption.checked = searchbarSettings.SearchBarDisplayAlways;
        }

        // Render extension settings.
        extensionSettings = proxyStore.getState().data.ExtensionSetting;
        if (extensionSettings) {
            defaultOption.checked = extensionSettings.IsDefaultOpenRakutenWebsearch;
            searchHistoryOption.checked = extensionSettings.IsSearchHistoryStored;
            runaBtaEndpoint.value = extensionSettings.RakutenBtaEndpoint || null;

            let check_radio_setting_by_index = function(index, radio_group) {
                const list = radio_group.querySelectorAll('input[type=radio]');
                if (index >= 0 && index < list.length) {
                    list[index].checked = true;
                }
            };
            let check_radio_setting_by_value = function(value, radio_group) {
                const list = radio_group.querySelectorAll('input[type=radio]');
                for (let i=0; i < list.length; i++) {
                    if (parseInt(list[i].value) == value) {
                        list[i].checked = true;
                        break;
                    }
                }
            };

            let select_setting = function(id, indexToSelect) {
                const element = document.getElementById(id);
                element[indexToSelect].selected = true;
            }
            check_radio_setting_by_value(extensionSettings.BannerDisableOption, rebates_options);
            check_radio_setting_by_index(extensionSettings.AffiliateOption, affiliate_options);
            if(extensionSettings.PickupDisabledTimerSelectedOption) {
                //the feature might be off based on A/B Test
                if(options_value_search && extensionSettings.BannerDisableOption == BANNER_DISABLE_OPTION.BANNER_DISABLE_SEARCH) {
                    select_setting("options_value_search", extensionSettings.PickupDisabledTimerSelectedOption);
                }
                else if (extensionSettings.BannerDisableOption == BANNER_DISABLE_OPTION.BANNER_DISABLE_ALL) {
                    select_setting("options_value_all", extensionSettings.PickupDisabledTimerSelectedOption);
                }
            }
        }

        // Add `Save` button handler: apply the changes.
        const saveButton = document.getElementById("save_setting");
        saveButton.addEventListener("click", function() {

            // take old setting for comparison
            let currentSearchbarSetting = proxyStore.getState().data.SearchBarSetting;

            searchbarSettings.SearchBarHighlight = highlightOption.checked;
            searchbarSettings.SearchBarDisplayAlways = fixedOption.checked;
            proxyStore.dispatch({ type: 'SET_SEARCHBAR_SETTING', data: searchbarSettings });
            GoogleAnalyticsReportForUpdateSetting(currentSearchbarSetting, searchbarSettings);

            // take old setting for comparison
            let currentExtSetting = proxyStore.getState().data.ExtensionSetting;

            extensionSettings.IsDefaultOpenRakutenWebsearch = defaultOption.checked;
            extensionSettings.IsSearchHistoryStored = searchHistoryOption.checked;

            const listRebates = rebates_options.querySelectorAll('input[type=radio]');
            let valueRebates = BANNER_DISABLE_OPTION.NO_USE;
            for (let i=0; i < listRebates.length; i++) {
                if (listRebates[i].checked) {
                    valueRebates = parseInt(listRebates[i].value);
                    break;
                }
            }
            extensionSettings.BannerDisableOption = valueRebates;
            if (valueRebates == BANNER_DISABLE_OPTION.BANNER_DISABLE_SEARCH) {
                extensionSettings.PickupDisabledTimerSelectedOption = options_value_search.selectedIndex;
            }
            else if (valueRebates == BANNER_DISABLE_OPTION.BANNER_DISABLE_ALL) {
                extensionSettings.PickupDisabledTimerSelectedOption = options_value_all.selectedIndex;
            }
            else {
                extensionSettings.PickupDisabledTimerSelectedOption = null;
            }

            const listAffiliates = affiliate_options.querySelectorAll('input[type=radio]');
            let indexAffiliates = AFFILIATE_SETTING_OPTION.DO_NOT_SHOW_BAR;
            for (let aso in AFFILIATE_SETTING_OPTION) {
                const affiliate_setting_value = AFFILIATE_SETTING_OPTION[aso];
                if (listAffiliates[affiliate_setting_value.RADIO_INDEX].checked) {
                    indexAffiliates = affiliate_setting_value.RADIO_INDEX;
                    extensionSettings.AffiliateOption = indexAffiliates;
                    break;
                }
            }

            extensionSettings.RakutenBtaEndpoint = runaBtaEndpoint.value.length > 0 ? runaBtaEndpoint.value : null;

            proxyStore.dispatch({ type: 'SET_EXTENSION_SETTING', data: extensionSettings });
            GoogleAnalyticsReportForUpdateSetting(currentExtSetting, extensionSettings);
            sendMessageByPort(port, {title: "SavePreferences"});
        });
    }
})();
